*, *:before, *:after {
  box-sizing: border-box; }

* {
  padding: 0;
  margin: 0; }

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: #ffffff;
  font: 300 16px "AlegreyaSC", serif;
  line-height: 1.4;
  color: #000000; }

ul, ol {
  padding: 0;
  margin: 0; }

a {
  outline: none;
  color: #000000;
  transition: all .3s;
  text-decoration: none; }
  a:hover, a:focus {
    outline: none; }
  a:hover {
    text-decoration: underline; }

img {
  max-width: 100%;
  height: auto; }

body, html {
  height: 100%;
  font-size: 16px; }

.globwrap {
  min-height: 100%; }

p {
  margin: 0 0 10px 0; }

.bgimg {
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover; }

.bgimgcont {
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain; }

.dib {
  display: inline-block;
  vertical-align: top; }

.d-table {
  display: table;
  width: 100%; }

.d-cell {
  display: table-cell;
  vertical-align: middle;
  text-align: center; }

.slick-slide {
  outline: none; }
  .slick-slide img {
    display: inline-block; }

.contenttab {
  display: none; }
  .contenttab.activetab {
    display: block; }

.opacity-0 {
  opacity: 0; }

.opacity-1 {
  opacity: 1; }

.wrap {
  max-width: 1200px;
  margin: 0 auto;
  position: relative; }

.hidden {
  display: none; }

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  font-family: "AlegreyaSC", serif;
  font-weight: 500; }

h1 {
  font-size: 30px;
  margin-bottom: 40px;
  text-transform: uppercase; }

h2 {
  font-size: 30px;
  margin-bottom: 40px; }

h3 {
  font-size: 30px; }

h4 {
  margin-bottom: 18px; }

.modal_active {
  overflow: hidden; }

.modal_div {
  width: 800px;
  height: 448px;
  position: fixed;
  top: 45%;
  /* отступаем сверху 45%, остальные 5% подвинет скрипт */
  left: 50%;
  margin-left: -400px;
  margin-top: -224px;
  display: none;
  z-index: 999;
  opacity: 0;
  background: #ffffff; }
  .modal_div .modal_close {
    width: 32px;
    height: 32px;
    position: absolute;
    top: 17px;
    right: -50px;
    cursor: pointer;
    display: block; }

/* Подложка */
.modal_overlay {
  z-index: 99;
  position: fixed;
  background-color: #000;
  opacity: 0.8;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  cursor: pointer;
  display: none; }

.section {
  position: relative; }

.header {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 27px 0; }

.logo__header {
  width: 230px;
  margin: 0 auto; }

.section-top-banner {
  padding: 150px;
  background: url("../../dist/images/background.jpg") no-repeat;
  background-size: cover; }

.content-wr-block {
  position: relative;
  max-width: 970px;
  width: 100%;
  margin: 0 auto; }

.count-wrapp {
  display: flex;
  font-size: 88px;
  font-family: "Open Sans", sans-serif;
  color: #c81818; }

.item-count {
  height: 115px;
  width: 115px;
  line-height: 115px;
  text-align: center; }
  .item-count:nth-child(2) {
    background: url("../../dist/images/2.png") no-repeat;
    background-size: 100% 100%; }
  .item-count:nth-child(3) {
    background: url("../../dist/images/3.png") no-repeat;
    background-size: 100% 100%; }
  .item-count:nth-child(4) {
    background: url("../../dist/images/4.png") no-repeat;
    background-size: 100% 100%; }
  .item-count:nth-child(5) {
    background: url("../../dist/images/2.png") no-repeat;
    background-size: 100% 100%; }
  .item-count:nth-child(6) {
    background: url("../../dist/images/3.png") no-repeat;
    background-size: 100% 100%; }
  .item-count:nth-child(7) {
    background: url("../../dist/images/3.png") no-repeat;
    background-size: 100% 100%; }
  .item-count:nth-child(8) {
    background: url("../../dist/images/2.png") no-repeat;
    background-size: 100% 100%; }
  .item-count:last-child {
    background: url("../../dist/images/4.png") no-repeat;
    background-size: 100% 100%; }

.dollars {
  color: #1d5911;
  background: url("../../dist/images/1.png") no-repeat;
  background-size: cover; }

.slogan-block {
  max-width: 345px;
  font-size: 31px;
  line-height: 1.2;
  font-family: "AlegreyaSC", serif;
  font-weight: 400;
  text-transform: uppercase;
  color: #ffffff;
  margin: 0 auto; }

.jackpot {
  color: #dbc25c;
  text-shadow: 3px 6px 3px rgba(0, 0, 0, 0.3); }

.gold-txt {
  font-size: 100px;
  text-transform: uppercase;
  line-height: 2.259;
  text-shadow: 3px 6px 3px rgba(0, 0, 0, 0.3);
  color: #e9b912;
  margin: 0 20px; }

.block-txt {
  display: flex;
  align-items: center;
  max-width: 511px;
  justify-content: space-between;
  margin: 0 auto;
  font-size: 36px;
  font-weight: 400;
  color: #fdfed6; }

.wr-code {
  text-align: center;
  width: 270px;
  margin: 0 auto; }

.code-txt {
  font-size: 13px;
  color: #ffffff;
  line-height: 3.1; }

.this-code {
  width: 265px;
  height: 70px;
  line-height: 70px;
  font-size: 25px;
  text-align: center;
  color: #fdfed6;
  background: url("../../dist/images/polygon.png") no-repeat;
  margin-bottom: 25px; }

.btn-claim-now {
  display: block;
  width: 265px;
  height: 70px;
  line-height: 70px;
  text-align: center;
  text-transform: uppercase;
  font-size: 25px;
  color: #700900;
  background: url("../../dist/images/btn.png") no-repeat;
  margin: 0 auto; }

.spin-down {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 50px;
  left: 0;
  right: 0;
  text-align: center; }

.item-spin {
  width: 8px;
  height: 8px;
  margin: 5px;
  border-radius: 50%;
  background: #a9706a; }
  .item-spin:first-child {
    animation: spin 1s infinite; }
  .item-spin:nth-child(2) {
    width: 8px;
    height: 8px;
    animation: spin 1s infinite 1.5s; }
  .item-spin:last-child {
    width: 16px;
    height: 16px;
    animation: spin 1s infinite 2.5s; }

@keyframes spin {
  0% {
    width: 8px;
    height: 8px; }
  50% {
    width: 12px;
    height: 12px; }
  100% {
    width: 16px;
    height: 16px;
    background: #ffffff; } }

.section-slider {
  padding: 50px 0;
  background: #320e00; }

.section-cap {
  font-size: 22px;
  font-weight: 300;
  text-align: center;
  color: #ffffff;
  background: #320e00; }

.slider-gallery {
  max-width: 1285px;
  width: 100%;
  padding: 0 10px;
  margin: 0 auto; }

.item-slide {
  position: relative;
  width: 270px;
  height: 200px;
  border-radius: 9px;
  overflow: hidden; }

.item-slide_overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(175, 6, 2, 0.8); }

.btn-white {
  display: block;
  width: 200px;
  height: 40px;
